<template>
  <div class="section-bot">
    <div id="main-content" class="container">
      <div class="row">
        <div class="col-md-6">
          <form class="form-inline">
            <div class="form-group">
              <label for="connect"> :</label>
              <button
                v-show="isConnect"
                class="btn btn-default"
                type="submit"
                @click="connect"
              >
                Bot 
              </button>
              <!-- <button
                v-show="!isConnect"
                class="btn btn-default"
                type="submit"
                @click="disconnect"
              >
                
              </button> -->
            </div>
          </form>
        </div>
        <div class="col-md-6">
          <form class="form-inline" @submit.prevent>
            <!-- <div class="form-group"> -->
            <!-- <label for="msg"></label> -->

            <input
              type="text"
              v-model="msg"
              class="form-control"
              placeholder=" ...."
            />

            <!-- </div> -->
            <button
              v-show="!isConnect"
              class="btn btn-default"
              type="submit"
              @click="sendMessage"
            >
              
            </button>
          </form>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <table class="table table-striped" v-show="isConnect">
            <thead>
              <tr>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(msg, index) in message" :key="index">
                <td>{{ msg }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";

export default {
  name: "chat-page",
  components: {},
  data() {
    return {
      // stompClient: null,
      message: [],
      msg: "",
      isConnect: true,
    };
  },
  methods: {
    // setConnected(connected) {
    //   //   $("#connect").prop("disabled", connected);
    //   //   $("#disconnect").prop("disabled", !connected);
    //   //   $("#send").prop("disabled", !connected);
    //   this.connected = connected;
    //   //   if (connected) {
    //   //     $("#conversation").show();
    //   //   } else {
    //   //     $("#conversation").hide();
    //   //   }
    //   //   $("#msg").html("");
    //   this.msg = "";
    // },
    connect() {
      var socket = new SockJS("/ws");
      this.stompClient = Stomp.over(socket);
      this.stompClient.connect({}, (frame) => {
        // this.setConnected(true);
        // this.connected = true;
        this.isConnect = true;
        console.log("Connected: " + frame);

        this.stompClient.subscribe("/topic/public", (message) => {
          this.message.push(": " + message.body);
          //   this.showMessage(" : " + message.body); //     
        });
      });
    },
    disconnect() {
      if (this.stompClient !== null) {
        this.stompClient.disconnect();
      }
      // this.setConnected(false);
      // this.connected = false;
      this.isConnect = false;
      console.log("Disconnected");
    },
    sendMessage() {
      //   let message = $("#msg").val();
      //   this.showMessage(" : " + this.msg);
      this.message.push(": " + this.msg);
      this.stompClient.send("/sendMessage", JSON.stringify(this.msg), {}); //  
      console.log(JSON.stringify(this.msg));
      this.msg = "";
    },
    // showMessage(message) {
    //   $("#communicate").append("<tr><td>" + message + "</td></tr>");
    // },
  },
};
</script>


<style>
.section-bot {
  border-width: 10px;
  border-style: solid;
  border-color: #0291fb;
  background-color: #ffffff;
}

.form-control {
  text-align: center; /* Quirks Mode     */
}
</style>